import './styles/Body.css'; 

function Devices() {
  return (
    <div>
        <h2 class="header" style={{fontSize: 30, fontFamily: "Readex Pro"}}>
            Những thiết bị và ứng dụng cần thiết để có thể quay vlog hoặc podcast
        </h2>
        <div style={{padding:"0 80px"}}>
            <p>Thiết bị ghi âm: Bạn sẽ cần một chiếc microphone tốt, có khả năng thu được giọng nói của bạn một cách rõ ràng, cũng như không làm thay đổi quá nhiều chất giọng vốn có của bạn để người nghe có thể nghe được một cách lôi cuốn nhất Thường thì bạn có thể có mua được những chiếc microphone tốt như vậy với mức giá từ 1 triệu trở lên, những chiếc microphone khá tốt tiêu biểu có thể kể đến như Razer Seiren (khoảng giá 1,5tr), Sony XLR-K3M (khoảng giá 4tr) hoặc nếu bạn vừa hướng đến sự chuyên nghiệp và cả sự linh hoạt, tiện dụng thì bạn sẽ có một ứng cử viên tiềm năng là Rode Wireless Microphone trong tầm giá 7 triệu.</p>
            <p>Một chiếc camera : Hiện nay thì thật ra đã có rất nhiều loại camera của điện thoại có thể phục vụ tốt cho việc quay video của bạn rồi. Nhưng nếu bạn muốn có một chất lượng hình ảnh trong tầm tốt nhất, với màu sắc lung linh hoặc các tính năng chống rung cao cấp thì bạn nên mua thêm camera rời. Trong làng camera rời phục vụ quay vlog,... thì không thể không kể đến anh lớn GoPro. Bạn chỉ cần bỏ ra từ 5-6tr là đã có thể có một chiếc GoPro Hero 6 để có thể quay video với độ phân giải 4K với 30 khung hình trên 1 giây, nổi bật nhất của dòng GoPro là tính năng hỗ trợ chống rung vô cùng tuyệt vời để bạn có thể thực hiện việc quay video gần như là ở bất cứ đâu.</p>
            <p>Một chiếc thẻ nhớ hoặc ổ cứng đủ lớn để lưu trữ những nguyên vật liệu phục vụ tạo nên sản phẩm của bạn: Trong mảng này này có rất nhiều lựa chọn, bạn hãy chọn cái phù hợp nhất với nhu cầu.</p>
            <p>Những phần mềm chỉnh sửa âm thanh (autotune): Bạn có thể sử dụng Adobe Premiere, Canva, Final Cut, v.v, mảng này thì cũng có vô vàn sự lựa chọn dành cho bạn</p>
        </div>
    </div>
  );
}

export default Devices;