import './styles/Body.css'; 

function Vlog() {
  return (
    <div>
        <h2 class="header" id="vlog">
            Vlog: (video + blog/log)
        </h2>
        <div style={{padding:"0 80px"}}>
            <h3>Làm Vlog dễ hay khó?</h3>
            <p>Ngày nay, Vlogging đã trở thành một thứ bình thường mới. Ai ai cũng vlog, giới trẻ thực hiện nó, những người trung niên và cả các chuyên gia cũng bước theo trào lưu này. Vậy thực chất, Vlog là gì? Vlog, hiểu đơn giản, chính là tạo ra những video riêng mà từ đó họ có thể kể câu chuyện của mình, chia sẻ một điều gì đó mang lại giá trị.</p>
            <p>Làm Vlog tuy đơn giản nhưng không phải ai quay video up Youtube cũng được xem là Vlogger. Nhiều Youtuber hiện nay đã hiểu sai ý nghĩa của Vlog như là các video “Thử thách 24h...”. Một Vlogger thật sự phải thể hiện được quan điểm của mình qua những video qua xuyên suốt các chủ đề. Từ đó bạn cảm nhận được hành trình, câu chuyện của họ. Các Vlogger của Việt Nam có thể kể đến như: Giang Ơi, Khoai Lang Thang, Jevermind,...</p>
            <h3>Cần chuẩn bị gì để trở thành một Vlogger chuyên nghiệp?</h3>
            <p>Để trở thành một vlogger chuyên nghiệp và thu hút được người nghe, hoặc là bạn cần phải có một lượng kiến thức đủ sâu và chính xác về chủ đề bạn thực hiện, hoặc cách bạn chia sẻ vấn đề, câu chuyện phải lôi cuốn hoặc là chí ít khiến cho người nghe cảm thấy dễ dàng nắm bắt được nội dung của bạn và có một sự yêu thích dành cho bạn cũng như những câu chuyện bạn chia sẻ.</p>
            <p>Mặt khác, một khi đã xác định trở thành một Vlogger chuyên nghiệp. Tức là bạn hướng tới việc trở thành một influencer, bạn sẽ có một sự ảnh hưởng đáng kể đối với người nghe của bạn. Vì vậy nên những nội dung và thông tin bạn chia sẻ nên là những thông tin đã được xác thực hoặc nó chỉ nên thuần túy là những cảm nhận của bạn. Bạn không nên chia sẻ những nội dung sai sự thật hoặc chỉ dựa trên những cảm nghĩ của bạn và cho rằng đó là nội dung đúng.</p>
            <h3>Xác định chủ đề và đối tượng hướng đến Vlog của bạn</h3>
            <p>Điều đầu tiên khi làm Vlog cần nghĩ đến là bạn làm vì đơn giản thử một điều mới hay thực sự muốn theo đuổi sự nghiệp Vlogger. Sau đó hãy xác định xem Vlog cả mình sẽ nói về chủ đề gì (game, skincare, chia sẻ kiến thức, …..). Hãy chọn điều bạn yêu thích để làm chủ đề vì nó sẽ giúp bạn có nhiều động lực để quay video. Chạy theo những xu hướng hiện tại có thể nhận được lượng theo dõi ban đầu nhưng sẽ không có giá trị lâu dài. Ngoài ra bạn cũng nên định hướng đối tượng mà bản thân nhắm đến để chọn chủ đề cho phù hợp. </p>
            <h3>Bắt đầu quay ngay video đầu tiên của mình</h3>
            <p>Để bắt đầu video bạn có thể sử dụng chính camera trên điện thoại hoặc webcam máy tính để quay. Nếu muốn chuyên nghiệp hơn có thể dùng một số loại máy quay chuyên nghiệp. Hãy quay những thứ bạn thích và đưa cho những người quen của bạn xem phản ứng của họ ra sao trước khi chính thức bước chân vào con đường này.</p>
            <h3>Một số chú ý khi quay Vlog</h3>
            <p>Hãy tự nhiên nhất có thể: quay Vlog quan trọng nhất vẫn là sự tự nhiên, điều đó có thể khiến bạn tiếp cận mọi người một cách tốt hơn. Lên một kịch bản quá kĩ sẽ làm video thiếu sự chân thật</p>
            <p>Tương tác với người xem: việc tương tác nhiều với người xem sẽ tạo cảm giác gần gũi với người xem, từ đó người xem sẽ dễ cảm nhận với video của bạn hơn</p>
            <p>“Tự tin khoe cá tính”: hãy là chính bạn khi quay vlog vì người xem chỉ muốn xem những thứ đặc trưng của bạn. Hãy tập thể hiện sự tự tin khi nhìn vào máy quay để trở thành một Vlogger chuyên nghiệp</p>
            <h3>Đưa video lên nhiều nền tảng (Spotify)</h3>
            <p>Cuối cùng khi vlog của bạn đã hoàn thành và chỉnh chu nhất thì bạn chỉ còn phải ra mắt nó với công chúng thôi. Vì vlog của bạn sẽ quan trọng cả phần hình ảnh và âm thanh nên bạn không thể chọn các nền tảng như Spotify hay Soundcloud,... mà phải hướng đến những nơi chia sẻ video như Youtube hay TikTok hoặc thậm chí là Facebook và Twitter,... Sau đó thì là xong, bạn có thể chọn ngồi chờ cho video của mình được các thuật toán quảng cáo chạm đến, hoặc bạn có thể tiến 1 bước xa hơn thế nữa, đó chính là chủ động quảng bá cho vlog của mình bằng nhiều cách khác nhau.</p>
        </div>
    </div>
  );
}

export default Vlog;
